













































































































































































































































































































#project-configure {
	#info, #items, #spiders {
		.el-form {
			padding: 20px 30px;
		}
	}
	#info {
		.el-form {
			.el-form-item {
				margin-bottom: 0;
			}
		}
	}
}

.inline {
	display: inline-block;
	&.short {
		max-width: 100px !important;
	}
	&.medium {
		max-width: 200px !important;
	}
	&.long {
		max-width: 400px !important;
	}
}

#save {
	position: fixed;
	width: 40px;
	height: 40px;
	border-radius: 50%;
	right: 20px;
	bottom: 20px;
	z-index: 1000;
	font-size: 12px;
	padding-left: 15px;
}
