
































































































































































































































































































#console {
	height: auto;
	border: 1px solid rgb(223, 230, 236);
	p {
		margin-top: 5px;
		margin-bottom: 5px;
		button {
			margin-right: 5px;
		}
		a {
			color: rgb(72, 87, 106);
			font-weight: 400;
		}
	}
	label {
		.el-button {
			width: 100%;
		}
	}
}

#follows {
	$max-height: 400px;
	#follow-requests, #follow-items {
		border: 1px solid rgb(223, 230, 236);
		overflow-y: scroll;
		max-height: $max-height;
		.el-loading-mask {
			height: 10000px;
		}
	}
	#follow-items {
		.follow-item {
			border-bottom: 1px solid rgb(223, 230, 236);
			.key {
				width: 10%;
			}
			.value {
				width: 90%;
			}
		}
	}
	#follow-requests {
		.follow-request {
			position: relative;
			height: 40px;
			line-height: 40px;
			padding-left: 5px;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
			cursor: pointer;
			border-bottom: 1px solid rgb(223, 230, 236);
		}
	}

	#follow-web {
		iframe {
			border: 1px solid #dfe6ec;
			min-height: 500px;
		}
	}

	#follow-html {
		max-height: 500px;
		overflow: scroll;
		border: 1px solid #dfe6ec;
		padding: 15px;
	}
}

.el-tabs__item.is-active {
	color: #28ccaa !important;
}

.el-tabs__active-bar {
	background-color: #28ccaa !important;
}

